/* Background.css */
body {
    overflow-x: hidden;
  }
.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #121212d3; /* Change to your desired background color */
    z-index: -1; /* Place the background behind everything */
}

@media (max-width: 1000px) {
    .background {
        display: none; /* Hide the background on mobile devices */
    }
}